import React from "react";
import {Card, Col } from "react-bootstrap";
import {Link} from "react-router-dom";

import PropTypes from 'prop-types';
import {getFullAPIAddress} from "../../utils/api";


export const ServiceCard = (props) => {

    return <Col sm={6} md={4} lg={3} style={{marginBottom: 20}}>
                <Card style={{minHeight: 340, backgroundColor: "#FFFFEE"}}>
                    <Card.Img top width="100%" src={ getFullAPIAddress(props.icon) } alt="Card image cap" />
                    <Card.Body>
                      <Card.Title>
                          <Link to={"/services/group/" + props.id }>
                          { props.name }
                          </Link>
                      </Card.Title>
                      <Card.Text>{ props.date } </Card.Text>
                      <Card.Text>
                        <small className="text-muted">Last updated 3 mins ago</small>
                      </Card.Text>
                    </Card.Body>
                </Card>
            </Col>
};


ServiceCard.propTypes = {
    icon: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    id: PropTypes.number.isRequired,
    date: PropTypes.string.isRequired
};


export default ServiceCard;