import React, { Component } from "react";
import {Link} from "react-router-dom";

import {Card, Tab, Nav, Container, Row, Button, Col, CardImg, Table} from "react-bootstrap";
import TableSubHeader from "../../components/ListViews/TableSubHeader";
import {numberWithCommas} from "../../utils/formaters";
import TableListView from "../../components/ListViews/TableListView";
import ButtonLink from "../../components/ButtonLink";


class ListHelper extends Component {

    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data) {
         let lastYear=2100;

         const items = Object.values(data)
            .sort(this.sort)
            .map(item => {
                let header;
                const year = parseInt(item.date.substr(0,4));
                // Generates header when we found new service type
                if (year != lastYear) {
                    lastYear = year;
                    header = <TableSubHeader colSpan={9} header={year} align={"center"}/>
                }
                return [ header, this.renderOneItem(item) ];
                });

         return items;
    }

    /**
     * Sets sort criteria
     * For this case: by Date
     */

    sort = (a,b) => (a.date > b.date) ? -1: 1;

    /** Render a Line of Data  */

    renderOneItem = (item) => {

        const toolBar = (this.props.renderLineToolbar) ? <td>{this.props.renderLineToolbar(item.id, this.props.updateData)}</td> : undefined;
        return <>
            <tr onClick={() => { this.props.history.push(`/projects/${item.id}`); }} >
                <td>{ item.date }</td>
                <td>{ item.name }</td>
                <td align="center">{ numberWithCommas(item.budget) }</td>
                <td align="center">{ numberWithCommas(item.budget_fact) }</td>
                <td></td>
           </tr>
        </>
    };

     /** Render Toolbar */
    renderToolbar() {
        return  <ButtonLink to={`/deals/${this.props.id}/projects/new/`} title={"+ Add"} />
    }


    /** Renders component with Infinite Scroll*/

    render() {
        const items = this.renderItems(this.props.data);

        const tableHeader = [
            {name: "", width: "20%"},
            {name: "", width: "50%"},
            {name: "", width: "15%", align: "center"},
            {name: " ", width: "15%", align: "center"},
        ];


        const toolbar = this.renderToolbar();

        return <TableListView toolbar={toolbar} tableHeader={tableHeader} items={items} />

    }

};

