import React, { Component } from "react";
import * as Yup from 'yup';
import {Container} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import FormikForm from "../../components/Forms/FormikForm"
import {api, resource} from "./_config";


// Todo:   modal

class FormView extends Component {

    render() {

        const fieldsList = {
            cost: {
                  title: '',
                  sm: 12,
                  validation: Yup.number().required('Required')
              },

            supplier: {
              title: '',
              sm: 12,
              type: 'autocomplete',
              resource: 'suppliers',
              api: '/api/suppliers/select/'
            },

            comment: {
              title: '',
              sm: 12,
              validation: Yup.string()
            },

        };
         
        const backLink = "/options/" + this.props.data.option + "/cont_options";
        return (
            <div>
            <PageHeader
                header = { `   ${this.props.data.num}  ${this.props.data.date}` }
                subHeader =  { this.props.data.comment }
            />
            <Container fluid style={{ paddingLeft: 40, paddingRight: 40}}>

            <FormikForm fieldList = {fieldsList}
                        onSubmit = {this.onSubmit}
                        initialValues = {this.props.data}
                        onSuccessLink = {backLink}
                        {...this.props}
              />
        </Container>
      </div>

    );

    }

};


export default (props) => {
    let new_api;
    if ((props.match.params.id === 'new') && (props.match.params.deal_id !== undefined)) {
       new_api = `/api/options/${props.match.params.option_id}/cont_options/`;
    }

    const Wrapper = DetailDataLoaderWrapper(FormView, api,  resource, new_api);
    return <Wrapper {...props}/>;
}