import React, { Component } from "react";
import {Badge, OverlayTrigger} from "react-bootstrap";
import {Link} from "react-router-dom";
import GroupAddDelete, {GroupDelete} from "./GroupAddDelete";


class PopupWidget extends Component {

    renderItems(data) {

         const items = Object.values(data)
            .map(item => {
                return this.renderOneItem(item);
                });

         return items;
    }

    renderOneItem = (item) => {
        return <OverlayTrigger trigger="hover" placement="right" overlay={GroupDelete(item)} delay={1000}>
            <Link to={"/services/group/" + item.id}>
                <Badge variant="primary"
                       style={{marginRight: 7}}
                      >
                    { item.name }
                </Badge>
            </Link>
          </OverlayTrigger>
    }

    render() {

        const items = this.renderItems(this.props.data.groups)
        return <>{items}
            <GroupAddDelete {...this.props}>AAA</GroupAddDelete>
                </>


        }

};



export default PopupWidget;