import React, { Component } from "react";
import { MdEdit } from "react-icons/md";

import DeleteDialog from "../../components/DataUpdaters/DeleteDialog_OLD";
import TableListView from "../../components/ListViews/TableListView";
import FormModal from "./FormModal";
import {api, resource} from "./_config";
import WindowWidget from "../../components/WindowWidget";
import ButtonLink from "../../components/ButtonLink";

/**
 * Contacts List Widget manages contacts widget in Contractor's card
 *
 * @author: Mikhail Lazarev
 */

class ListWidget extends Component {
    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data) {
         let num=0;

         const items = Object.values(data)
            .sort(this.sort)
            .map(item => this.renderOneItem(num++, item)
                );

         return items;
    }

    /**
     * Sort items lambda function
     *
     * For finances we use service_type to group items by service type
     *
     */

    sort = (a,b) => {
        if (a.last_update > b.last_update) return -1;
        return 1;
    }


    /** Render Items (with header if needed) */

    renderOneItem(num, item) {

         return <tr >

                <td  width="95%">
                     <strong>{ item.name }</strong> <br />
                        { item.phone } <br />
                        { item.email } <br />
                        { item.web }
                </td>

                <td>
                    <FormModal data={item} id={item.id}>
                      <MdEdit />
                    </FormModal> &nbsp;

                    <DeleteDialog
                        api={api}
                        resource={resource}
                        id={item.id}
                        updateData = { this.props.updateData }
                        />
                </td>
           </tr>
    }

    /** Render component function */

    render() {

        const items = this.renderItems(this.props.data.contacts);
        const rightToolbar =  <ButtonLink to={`/suppliers/${this.props.data.id}/contacts/new/`} title={"+ "} />

        return  <WindowWidget title={""} rightToolbar={rightToolbar}>
                    <TableListView items={items} />
                </WindowWidget>
    }

};



export default ListWidget;