import React, { Component } from "react";

import {ListDataLoader} from "../../components/DataLoaders/DataLoaderList";
import ListHelper from "./ListHelper";
import MergeDialog from "../../components/DataUpdaters/MergeDialog"
import {api, resource} from "./_config";


class DuplicatesWidget extends Component {

    /**
     * Defines Line Toolbar for delete operations
     * @param id - id for Line
     * @returns JSX code which will be places in <td> </td> component
     */

    renderLineToolbar = (id, updateData) => {
        return <MergeDialog api={api}
                            resource={resource}
                            id={this.props.id}
                            alias_id={id}
                            updateData={updateData}/>
    }

    render() {

        const resource_duplicates = "suppliers_duplicates";
        const api_duplicates = "/api/suppliers/" + this.props.id + "/duplicates/";

        return <ListDataLoader api={api_duplicates} resource={resource_duplicates}>
            <ListHelper
                renderLineToolbar={this.renderLineToolbar}
                {...this.props} />
        </ListDataLoader>

    }
}


export default DuplicatesWidget