import React from "react";
import WindowWidget from "../../components/WindowWidget";
import ButtonLink from "../../components/ButtonLink";

import { numberWithCommas } from "../../utils/formaters";
import {resource} from "./_config";


export const InfoWidget = (props) => {

    const rightToolbar = <ButtonLink to={"/" + resource + "/" + props.id + "/edit"} title={"Edit"} />;

    return  <WindowWidget title={""} rightToolbar={rightToolbar}>
                 : { props.data.name }<br/>
                 : { props.data.int_name }<br/>
                  : { numberWithCommas(props.data.price) } <br/>
                 : { numberWithCommas(props.data.cost) } <br/>
                :<br/> { props.data.tz } <br/>
            </WindowWidget>

};

export default InfoWidget;