import React, { Component } from "react";
import {connect} from "react-redux";
import {Card, Tab, Nav, Container, Row, Button, Col, CardImg, Table} from "react-bootstrap";
import {Link} from "react-router-dom";
import { MdEdit } from "react-icons/md/index";
import FormModal from "./FormModal";
import {numberWithCommas} from "../../utils/formaters";
import * as actions from "../../actions/modals";
import TableListView from "../../components/ListViews/TableListView";
import TableSubHeader from "../../components/ListViews/TableSubHeader";
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog";
import {api, resource} from "./_config";

/**
 * FinanceList manages whole finance table
 *
 * @author: Mikhail Lazarev
 */

class ListView extends Component {

    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data) {
         let service_num=0, counter=0, sub_counter=1;

         const items = Object.values(data)
            .sort(this.sort)
            .map(item => {
                let header;

                // Generates header when we found new service type
                if (item.service_type > service_num) {
                    service_num = item.service_type;
                    sub_counter=1;
                    counter++;
                    header = <TableSubHeader colSpan={9}
                                             header={item.service_type_display}
                                             align={"center"}/>
                }
                const num = counter + "." + sub_counter++ + ".";
                return [ header, this.renderOneItem(num, item) ];
                });

         return items;
    }

    /** Sort items lambda function */
    sort = (a,b) => (a.service_type > b.service_type) ? 1: -1;

    /** Render Items (with header if needed) */
    renderOneItem = (num, item) => {

        const funcShowForm = () => this.props.showForm("financeModal", item);
        
        return <tr>
            <td>{ num }</td>
            <td onClick={funcShowForm}>{ item.name }</td>
            <td onClick={funcShowForm}>{ item.contractor_display }</td>
            <td onClick={funcShowForm} align="center">{ item.qty }</td>
            <td onClick={funcShowForm} align="center">{ numberWithCommas(item.official_sum)}</td>
            <td align="center">{ item.payment_type_display}</td>
            <td align="right">{ numberWithCommas(item.projected_sum)}</td>
            <td align="right">{ numberWithCommas(item.planned_sum)}</td>
            <td align="right">{ numberWithCommas(item.sum)}</td>
            <td>
                <MdEdit onClick={funcShowForm} />
                <DeleteDialog api={api} resource={resource} id={item.id} updateData={this.props.updateData} />
            </td>
        </tr>

    };

    /** Render Toolbar */
    renderToolbar = () => (
            <Button size={"sm"}
                    onClick={() => this.props.createForm("financeModal", "/api/finance_set/" + this.props.data.id + "/finances/new/")}>
                + </Button>
    );

    /** Render component function */
    render() {

        const data = (this.props.data.f_items !== undefined) ? this.props.data.f_items : this.props.data;
        const modalForms =  <FormModal formId={"financeModal"} updateData={this.props.updateData} />
        const toolbar = this.renderToolbar();

        const tableHeader = [
            {name: "#", width: "1%"},
            {name: " ", width: "25%"},
            {name: "", width: "14%"},
            {name: "-", width: "10%", align: "center"},
            {name: ". ", width: "10%", align: "center"},
            {name: "", width: "10%", align: "center"},
            {name: "", width: "10%", align: "center"},
            {name: "", width: "10%", align: "center"},
            {name: "", width: "10%", align: "center"},
            {name: "", width: "8%", align: "center"},
        ];

        const items = this.renderItems(data);

        return <TableListView modalForms={modalForms} toolbar={toolbar} tableHeader={tableHeader} items={items} />

    }

};

const mapDispatchToProps = dispatch => ({
     createForm:      (formId, api) => dispatch(actions.createForm(formId, api)),
     showForm:      (formId, data) => dispatch(actions.showForm(formId, data))

});


export default connect(null, mapDispatchToProps)(ListView);
