import React from "react";
import {Button, Container, Nav, Tab, Row, Col} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import TabBar from "../../components/TabBar/TabBar";

import InfoWidget from  "./InfoWidget";
import ContactsListWidget from "../Contacts/ListWidget";
import OptionsListWidget from "../Options/ListWidget";
import PayersListWidget from "../Payers/ListWidget";

import FinanceList from "../Finances/ListView";
import DuplicatesWidget from "./DuplicatesWidget";

import {api, resource} from "./_config";


const DetailView = (props) => {

    return <>
            <PageHeader header={props.data.name} subHeader={props.data.job}/>

            <TabBar id={props.id} resource={resource} tab={props.match.params.tab} history={props.history} >
                <Tab.Pane eventKey="info" label={""}>
                    <Row>
                        <Col sm={8}><InfoWidget {...props} /></Col>
                        <Col sm={4}><ContactsListWidget data={props.data} updateData={props.updateData}/></Col>
                    </Row>
                </Tab.Pane>

                <Tab.Pane eventKey={"options"} label={""}>
                    <OptionsListWidget id = { props.id } {...props} data={props.data.options} />
                </Tab.Pane>

                <Tab.Pane eventKey="payers" label={""}>
                    <PayersListWidget id = { props.id } {...props} data={props.data.payers} />
                </Tab.Pane>

                <Tab.Pane eventKey="finances" label={""}>
                    <FinanceList id = { props.id } {...props} data={props.data.finances} />
                </Tab.Pane>

                <Tab.Pane eventKey="duplicates" label={""}>
                    <DuplicatesWidget {...props}/>
                </Tab.Pane>
             </TabBar>

        </>
};

export default DetailDataLoaderWrapper(DetailView, api, resource);