import React, { Component } from "react";
import {Col, Container, Row} from "react-bootstrap";

import ListDataLoader from "../../components/DataLoaders/DataLoaderList"
import PageHeader from "../../components/PageHeader";
import ProjectCard from "./ProjectCard"
import InfiniteScroll from "react-infinite-scroller";
import {api, resource} from "./_config";

class ListView extends Component {

    render() {

        console.log(this.resource)
        let prev_date = "2100-01";
        const items = Object.values(this.props.data)
            .sort((a,b) => {
                if (a.date > b.date) return -1;
                return 1;
            })
            .map(item => {
                const month_date = item.date.substr(0, 7)
                if (prev_date > month_date) {
                    prev_date = month_date
                    return <>
                            <Col sm={12}>{month_date}</Col>
                        <ProjectCard
                            key={"EC" + item.id}
                            {...item}/>
                    </>
                }
                return <ProjectCard
                            key={"EC" + item.id}
                            {...item}/>
                        });

        return (
            <div>
            <PageHeader
                header = "Projects"
                subHeader = "SubHeader"
                resource={resource}
                searchBar
            />
            <Container fluid style={{paddingLeft: 40, paddingRight: 40}}>

                <InfiniteScroll
                    page={1}
                    loadMore={this.props.loadMore}
                    hasMore = {this.props.hasMore}

                    >
                     <Row>
                        {items}
                    </Row>
                </InfiniteScroll>

            </Container>

            </div>
        );

        }

};



export default ListDataLoader(ListView, api, resource);