import React, { Component } from "react";
import {connect} from "react-redux";
import {Container, Button, Table} from "react-bootstrap";
import {Link} from "react-router-dom";
import { MdEdit } from "react-icons/md/index";
import ServiceSelectModal from "../Services/ServiceSelectModal";

import {numberWithCommas} from "../../utils/formaters";
import FormModal from "./FormModal";
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog_OLD";
import CreateFinancesDialog from "./CreateFinancesDialog"
import TableListView from "../../components/ListViews/TableListView";
import TableSubHeader from "../../components/ListViews/TableSubHeader";

import * as actions from "../../actions/dataLoader";
import {api, resource} from "./_config";


class ListWidgetEvent extends Component {

    /**
     * Render data into JSX table
     * @param data
     * @param internal - show internal costs or customers prices
     * @param alt - alternative budget positions
     * @returns JSX table
     */

    renderItems(data, internal, alt) {
         let service_num=0, counter=0, sub_counter=1;

         // Sort items lambda function
         const sort = (a,b) => (a.service_type > b.service_type) ? 1: -1;

         const items = Object.values(data)
            .filter(item => item.alt===alt)
            .sort(sort)
            .map(item => {
                let header;

                // Generates header when we found new service type
                if (item.service_type > service_num) {
                    service_num = item.service_type;
                    sub_counter=1;
                    counter++;
                    header = <TableSubHeader colSpan={9} header={item.service_type_display} align={"center"}/>
                }

                const num = counter + "." + sub_counter++ + ".";
                return [header, this.renderOneItem(num, item, internal)]
                });

         return items
    }

    /** Render Items (with header if needed) */
    renderOneItem(num, item, internal) {

        const funcLinkOption = () => this.props.showForm("budgetModal", item);

        return <tr>
                <td>{ num }</td>
                <td><Link to={"/services/" + item.service}>{ item.service_display }</Link></td>
                <td><Link to={"/options/" + item.option}>{ item.option_display }</Link></td>

                <td align="center">{ (internal) ? numberWithCommas(item.cost) : numberWithCommas(item.price) }</td>
                <td align="center">{ numberWithCommas(item.qty) }</td>
                <td align="center">{ item.measure_display }</td>
                <td align="center">{ (internal) ? numberWithCommas(item.total_cost) : numberWithCommas(item.total_price) }</td>
                <td>
                    <MdEdit onClick={funcLinkOption} />
                    <DeleteDialog
                        api={api}
                        id={item.id}
                        updateData = { this.props.updateData }
                    />
                </td>
           </tr>

    }

    /** Render Toolbar */
    renderToolbar = () => (
        <>
            <ServiceSelectModal>
                <Button size={"sm"} style={{marginRight: 10}}>+ </Button>
            </ServiceSelectModal>
             <a href={`http://localhost:8000/budgets/${this.props.data.id}/export_tz/`}>
                <Button size={"sm"} style={{marginRight: 10}}> </Button>
            </a>
            <a href={`http://localhost:8000/budgets/${this.props.data.id}/export_budget/`}>
                <Button size={"sm"} style={{marginRight: 10}}> </Button>
            </a>
            {/*<CreateFinancesDialog api={api} resource={resource} id={this.props.id}/>*/}
        </>)

    /** Render Component */
    render() {

        const internal = (this.props.int===undefined) ? true: this.props.int;
        const modalForms =  <FormModal formId={"financeModal"} updateData={this.props.updateData} />
        const main_items = this.renderItems(this.props.data.b_items, internal, false);
        const alt_items = this.renderItems(this.props.data.b_items, internal, true);

        const tableHeader = [
            {name: "#", width: "1%"},
            {name: "", width: "25%"},
            {name: "", width: "25%"},
            {name: "", width: "11%", align: "center"},
            {name: "-", width: "11%", align: "center"},
            {name: ". .", width: "9%", align: "center"},
            {name: "", width: "10%", align: "center"},
            {name: "", width: "8%", align: "center"},
        ];
        const toolbar = this.renderToolbar();

        return <>
                 <TableListView modalForms={modalForms} toolbar={toolbar} tableHeader={tableHeader} items={main_items} />
                 <br /><br />
                 <h4></h4><br />
                 <TableListView tableHeader={tableHeader} items={alt_items} />
              </>
    }

};

const mapDispatchToProps = dispatch => ({
     getListDataLoader:      (api, resource, query) => dispatch(actions.getDataLoaderList(api, resource, query)),

});

export default connect(null, mapDispatchToProps)(ListWidgetEvent);