import React, { Component } from 'react';
import { Route, Switch, Redirect } from 'react-router';
import { Container } from "react-bootstrap";
import { withRouter } from 'react-router';
import { connect } from 'react-redux'

import AppBar from '../components/AppBar/AppBar'
import Footer from '../components/Footer'

import Login from "./Auth/Login";
import SignUp from "./Auth/SignUp";
import OAuthComplete from "./Auth/OAuthComplete";

import Page404 from "../components/Page404";
import {authErrors, isAuthenticated, refreshTime} from "../reducers";
import {logout} from "../actions/auth";

import ContOptionFormView from "./ContOptions/FormView";


import DealsDetailView from "./Deals/DetailView";
import DealsFormView from "./Deals/FormView";
import DealsListView from "./Deals/ListView"

import ProjectsListView from "./Projects/ListView"
import ProjectsDetailView from "./Projects/DetailView"
import ProjectsFormView from "./Projects/FormView";

import OptionsDetailView from "./Options/DetailView";
import OptionsFormView from "./Options/FormView";
import OptionsListWidget from "./Options/ListWidget";

import PayersDetailView from "./Payers/DetailView";
import PayersFormView from "./Payers/FormView";

import ServicesListView from "./Services/ListView";
import ServicesFormView from "./Services/FormView";
import ServicesDetailView from "./Services/DetailView";
import GroupsDetailView from "./Groups/GroupDetail";

import SuppliersListView from "./Suppliers/ListView";
import SuppliersDetailView from "./Suppliers/DetailView";
import SupplierFormView from "./Suppliers/FormView";
import ConfirmationModal from "../components/ConfirmationModal";





export class App extends Component {

  render() {
    console.log("PROPS", this.props)

    const routesNotAuthenticated = (
      <Switch>
          <Route path="/login/"  component={Login}/>
          <Route path="/signup/" component={SignUp}/>
          <Route exact path="/auth/google/complete/"
                 component={() => <OAuthComplete {...this.props} provider={'google-oauth2'}/>}
          />
          <Route path='*' component={Login}/>
      </Switch>);

    const routesAuthenticated = (
        <Switch>

            <Route exact path="/cont_options/:id" component={ ContOptionFormView } />

            <Route exact path="/deals/" component={ DealsListView } />
            <Route exact path="/deals/:id/edit" component={ DealsFormView } />
            <Route exact path="/deals/:id/:tab" component={ DealsDetailView } />
            <Route exact path="/deals/:id" component={ DealsDetailView } />
            <Route exact path="/deals/:deal_id/Projects/:id" component={ ProjectsFormView }  />

            <Route exact path="/projects/" component={ ProjectsListView } />
            <Route exact path="/projects/:id/edit" component={ ProjectsFormView } />
            <Route exact path="/projects/:id/:tab" component={ ProjectsDetailView } />
            <Route exact path="/projects/:id" component={ ProjectsDetailView } />

            <Route exact path="/options/" component={ OptionsListWidget } />
            <Route exact path="/options/:id/edit" component={ OptionsFormView } />
            <Route exact path="/options/:id/:tab" component={ OptionsDetailView } />
            <Route exact path="/options/:id" component={ OptionsDetailView } />
            <Route exact path="/options/:option_id/Projects/:id" component={ ProjectsFormView }  />
            <Route exact path="/options/" component={ OptionsListWidget } />
            <Route exact path="/options/:id/edit" component={ OptionsFormView } />

            <Route exact path="/payers/:id/edit" component={ PayersFormView } />
            <Route exact path="/payers/:id" component={ PayersDetailView } />

            <Route exact path="/services/" component={ ServicesListView } />
            <Route exact path="/services/group/:id" component={ GroupsDetailView } />
            <Route exact path="/services/:id/edit" component={ ServicesFormView } />
            <Route exact path="/services/:id/:tab" component={ ServicesDetailView } />
            <Route exact path="/services/:id" component={ ServicesDetailView } />
            <Route exact path="/services/:service_id/options/:id" component={ OptionsFormView }  />

            // Suppliers
            <Route exact path="/suppliers/" component={ SuppliersListView } />
            <Route exact path="/suppliers/:id/edit" component={ SupplierFormView } />
            <Route exact path="/suppliers/:id/:tab" component={ SuppliersDetailView } />
            <Route exact path="/suppliers/:id" component={ SuppliersDetailView } />
            <Route exact path="/suppliers/:supplier_id/payers/:id" component={ PayersFormView } />

            <Route exact path="/" render={() => (<Redirect to="/projects"/>)}/>
            <Route exact path="/login" render={() => (<Redirect to="/projects"/>)}/>
            <Route exact path="/auth/*" render={() => (<Redirect to="/projects"/>)}/>
            <Route path='*' component={ Page404 }/>
        </Switch>);


    return <>
            <ConfirmationModal />
            <AppBar onLogout={this.props} {...this.props} />
            { this.props.isAuthenticated ? routesAuthenticated : routesNotAuthenticated }
            <Footer {...this.props}/>
        </>
  }
}

const mapStateToProps = (state) => ({
    errors:          authErrors(state),
    isAuthenticated: isAuthenticated(state),
    refreshTime:     refreshTime(state)

})

const mapDispatchToProps = dispatch => {
  return {
        onLogout:               () => dispatch(logout())
  }
};


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
