import React, {Component} from "react";
import {numberWithCommas} from "../../utils/formaters";
import TableSubHeader from "../../components/ListViews/TableSubHeader";
import InfiniteScrollListView from "../../components/ListViews/InfiniteScrollListView";


class ListHelper extends Component {

    /** Renders data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data) {
         let lastYear=2100;
         const sort = (a,b) => (a.date > b.date) ? -1 : 1;

         const items = Object.values(data)
            .sort(sort)
            .map(item => {
                let header;
                const year = parseInt(item.date.substr(0,4));
                // Generates header when we found new service type
                if (year != lastYear) {
                    lastYear = year;
                    header = <TableSubHeader colSpan={9} header={year} align={"left"}/>
                }
                return [ header, this.renderOneItem(item) ];
                });

         return items;
    }

    /** Render a Line of Data  */
    renderOneItem = (item) => {

        const toolBar = (this.props.renderLineToolbar) ? <td>{this.props.renderLineToolbar(item.id, this.props.updateData)}</td> : undefined;
        return <>
            <tr onClick={() => { this.props.history.push(`/deals/${item.id}`) }} >
                <td>{item.date}</td>
                <td>{item.name}</td>
                <td>{item.client_display}</td>
                <td align="right" style={{paddingRight: 40}}>{ numberWithCommas(item.sum_plan)}</td>
                <td align="right" style={{paddingRight: 40}}>{ numberWithCommas(item.sum_fact)}</td>
                {toolBar}
            </tr>
        </>
    };

    /** Renders component with Infinite Scroll*/
    render() {

        const items = this.renderItems(this.props.data)

        const tableHeader = [
            {name: "Data", width: "15%"},
            {name: "Name", width: "40%"},
            {name: "Client", width: "20%"},
            {name: "Sum planned", width: "13%", align: "center"},
            {name: "Sum fact", width: "13%", align: "center"},
        ];

        return <InfiniteScrollListView tableHeader={tableHeader} items={items}  {...this.props}/>
    }
};

