import React, { Component } from "react";
import {Card, Tab, Nav, Container, Row, Button, Col, ListGroup,Form} from "react-bootstrap";
import { Link } from "react-router-dom";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import ServiceCard from "../Services/ServiceCard";
import GroupCard from "./GroupCard";
import {api, resource} from "./_config";


class GroupDetail extends Component {

    get_items(data, type) {

        return  Object.values(data)
            .map(item => {

                if (type === 'group') {
                    return <GroupCard
                            key={"EC" + item.id}
                            {...item}/>

                }

                return <ServiceCard
                    key={"EC" + item.id}
                    {...item}/>

                });
    }


    render() {

        console.log("GROUPP", this.props.data)

        const groups = this.get_items(this.props.data.groups, 'group')
        const services = this.get_items(this.props.data.services, 'service')

        let header = this.props.data.name;

        if (this.props.data.parent_group) {
            header = <>
                        { this.props.data.name }&nbsp;
                        <Link to={"/services/group/" + this.props.data.parent_group}>..</Link>
                     </>;
        }


        return (
                <div>
                    <PageHeader
                        header={header}
                        subHeader= {this.props.data.job}
                        resource={resource + this.props.id}
                        searchBar
                    />

                    <Container fluid style={{paddingLeft: 40, paddingRight: 40}}>

                        <Row>
                            {groups} {services}
                        </Row>

                    </Container>

                </div>
                );

                }

};

export default DetailDataLoaderWrapper(GroupDetail, api, resource)