import React from "react";
import PropTypes from 'prop-types';
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog_OLD";
import FormModal from "./FormModal";
import {api} from "./_config";

export const BudgetLine = (props) => {
 // ['id', 'name', 'date', 'state', 'sum_plan'
    return <tr>
                <td>{ props.num }</td>
                <td style={{paddingRight: 20}}><strong>{ props.service_display_name }</strong><br />
                { props.option_display_name }
                </td>
                <td align="left" style={{paddingRight: 20}}>{ (props.tz) }</td>
                <td>
                    {/*<OptionFormModal id={props.option} style={{paddingRight: 15}}/>*/}
                    <FormModal data={props} style={{paddingRight: 15}}/>
                    <DeleteDialog
                        api={api}
                        id={props.id}
                        updateData = { props.updateData }
                    />
                </td>
           </tr>
};



export const BudgetHeader = (props) => {
 // ['id', 'name', 'date', 'state', 'sum_plan'
    return <thead>
                <tr>
                    <th width="2%">#</th>
                    <th width="24%"></th>
                    <th width="70%" style={{textAlign: 'center'}}></th>
                    <th width="4%" style={{textAlign: 'center'}}></th>
                </tr>
           </thead>
};


BudgetLine.propTypes = {
    int: PropTypes.bool.isRequired,

    id: PropTypes.number.isRequired,
    num: PropTypes.string.isRequired,
    service_display_name: PropTypes.string.isRequired,
    option_display_name: PropTypes.string.isRequired,
    price: PropTypes.string.isRequired,
    cost: PropTypes.string.isRequired,
    comment: PropTypes.string.isRequired

}

export default BudgetLine;