import React, { Component } from "react";
import PropTypes from "prop-types";
import * as Yup from 'yup';
import FormikModal from "../../components/Forms/FormikModal"
import {api, resource} from "./_config";

/**
 * Finances Forms describes form for finance module
 *
 */

class FormModal extends Component {

    render() {

        const fieldsList = {
            name: {
                title: ' ',
                validation: Yup.string().required()
            },

            contractor: {
                title: '',
                type: 'autocomplete',
                resource: 'suppliers',
                api: '/api/suppliers/select/'
            },

            service_type: {
                title: '',
                sm: 8,
                type: 'autocomplete',
                resource: 'service_type',
                api: '/api/config/service_types/'
            },

            date: {
                title: ' ',
                sm: 4,
                validation: Yup.date('Incorrect data format')
            },

            projected_sum: {
                title: '',
                sm: 6,
                validation: Yup.number()
            },

            payment_type: {
                title: ' ',
                sm: 6,
                type: 'select',
                resource: 'payment_types',
                options: {
                    NC: '.',
                    CA: '.',
                    IR: '. ',
                    WH: '',
                    CQ: '',
                    R1: ' 1',
                    R2: ' 2. ',
                    GP: ''
                },
                api: '/api/config/payment_types/'
            },

            planned_sum: {
                title: ' ',
                sm: 6,
                validation: Yup.number()
            },

            sum: {
              title: '',
              sm: 6,
              validation: Yup.number()
            },

        };

        return (

            <FormikModal fieldList = {fieldsList}
                         api = {api}
                         resource = {resource}
                         formId={this.props.formId}
                         updateData={this.props.updateData}
            />
         );

    }

};

FormModal.propTypes = {
    formId: PropTypes.string
}

export default FormModal