import React, { Component } from "react";
import * as Yup from 'yup';
import FormikModal from "../../components/Forms/FormikModal";
import {getApiById} from "../../utils/api";
import { MdEdit } from "react-icons/md";
import {api, resource} from "./_config";


class FormModal extends Component {

    render() {

        const fieldsList = {
            /*option: {
              title: '',
              type: 'autocomplete',
              resource: 'budget_options',
              api_id: (id) => getApiById(api + ':id/options/', id)
            },*/
            qty: {
              title: ' ',
              sm: 6,
              validation: Yup.number().required()
            },

            price: {
              title: ' ',
              validation: Yup.number().required()

            },

            cost: {
              title: ' ',
              validation: Yup.number().required()
            },

            comment: {
              title: '',
              validation: Yup.string()
            },

        };

        return <FormikModal fieldList={fieldsList}
                         api={api}
                         resource={resource}
                         formId={this.props.formId}
                         updateData={this.props.updateData}
            />

    }

};


export default FormModal;