import React, { Component } from "react";
import {Card, Tab, Nav, Container, Row, Button, Col, CardImg, Table} from "react-bootstrap";
import {Link} from "react-router-dom";
import ServiceSelectModal from "../Services/ServiceSelectModal";
import BudgetLineTZ, {BudgetHeader} from "./BudgetLineTZ";


class BudgetList extends Component {

    budget_items(data, internal) {
        let service_num=0, counter=0, sub_counter=1;
        return data .sort((a,b) => {
                if (a.service_type > b.service_type) return 1;
                return -1;
            })
            .map(item => {
                let header;
                if (item.service_type > service_num) {
                    service_num = item.service_type;
                    sub_counter=1;
                    counter++;

                    header = <tr style={{backgroundColor: "#FAFAFF"}}>
                                <td colSpan={9} align="center" style={{paddingTop: 12, paddingBottom: 14}}>
                                    <strong>{item.service_type_display_name}</strong>
                                </td>
                            </tr>
                }
                return (
                    <React.Fragment key={"ECD" + item.id}>
                        { header }
                        <BudgetLineTZ
                        num = { counter + "." + sub_counter++ + "." }
                        int = { internal }
                        {...item}
                    />
                    </React.Fragment>);
    });


    }

    render() {

        const internal = (this.props.int===undefined) ? true: this.props.int;

        const main_budget = this.budget_items(Object.values(this.props.data.b_items).filter(item => !item.alt), internal);
        const alt_budget = this.budget_items(Object.values(this.props.data.b_items).filter(item => item.alt), internal);


        return (
            <div>
            <Container fluid>
                <ServiceSelectModal>
                    <Button size={"sm"} style={{marginRight: 10}}>+ </Button>
                </ServiceSelectModal>
                 <a href={`http://localhost:8000/budgets/${this.props.data.id}/export_tz/`}>
                    <Button size={"sm"} style={{marginRight: 10}}> </Button>
                </a>
                 <a href={`http://localhost:8000/budgets/${this.props.data.id}/export_budget/`}>
                    <Button size={"sm"}> </Button>
                </a>


                <Table style={{marginTop: 14}} hover size={'sm'}>
                    <BudgetHeader/>
                    <tbody>{ main_budget }</tbody>
                </Table>
                <br /><br /><br />
                <h4></h4>
                 <Table style={{marginTop: 14}} hover>
                    <BudgetHeader/>
                    <tbody>{ alt_budget }</tbody>
                </Table>
            </Container>


            </div>
        );

        }

};



export default BudgetList;