import React from "react";
import queryString from 'query-string'
import { Redirect } from "react-router-dom";
import { authErrors, isAuthenticated } from "../../reducers";
import { oauthAuthenticate } from "../../actions/auth";
import {connect} from "react-redux";


export class GoogleAuthComplete extends React.Component {

    render () {

        const values = queryString.parse(this.props.location.search)
        console.log("CODE", values.code) // "top"

        if (values.code) {
            this.props.onCodeRecevived('google-oauth2', values.code)
            return <Redirect to={GoogleAuthComplete}/>
        }

        return (
            <div>

                Google Auth Complete


            </div>
        )
    }

}

const mapStateToProps = (state) => ({
  errors: authErrors(state),
  isAuthenticated: isAuthenticated(state)
})

const mapDispatchToProps = (dispatch) => ({

  onCodeRecevived: (provider, code) => { dispatch(oauthAuthenticate(provider, code)) }

})



export default connect(mapStateToProps, mapDispatchToProps)(GoogleAuthComplete);
