import React, {Component} from "react";
import {connect} from "react-redux";
import {Button} from "react-bootstrap";
import * as actions from "../../actions/dataLoader";
import UpdateWithConfirmation from "../../components/DataUpdaters/UpdateWithConfirmation";


class CreateFinanceDialog extends Component {
    render() {

        const buttonComponent = "AAA";
        //[<Button size={"sm"} style={{marginRight: 10}}> </Button>];

        return <UpdateWithConfirmation
                title={" "}
                body={`     ${this.props.id}  ${this.props.alias_id} ?   !`}
                buttonComponent={buttonComponent}
                actionName={""}
                onAction={(hash) => this.props.mergeItem(this.props.api, this.props.resource, this.props.id, this.props.alias_id, hash)}
                hashPrefix={'MERGE'}
                {...this.props}
            />

    }
}


const mapDispatchToProps = dispatch => ({
     mergeItem: (api, resource, id, alias_id, hashSent) => dispatch(actions.mergeDetailUpdateDetail(api, resource, id, alias_id, hashSent)),
});


export default connect(null, mapDispatchToProps)(CreateFinanceDialog);