import React, { Component } from "react";
import * as Yup from 'yup';
import {Card, Tab, Nav, Container, Row, Button, Col, ListGroup, Form, InputGroup} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import FormikForm from "../../components/Forms/FormikForm"
import {api, resource} from "./_config";


class FormView extends Component {

   render() {

        /*
          'name': '',
          'type': '',
          'google_drive': '  :'


         */

        const fieldsList = {
            name: {
                  title: '',
                  sm: 12,
                  validation: Yup.string().required('Required')
              },

            /*type: {
                  title: ' ',
                  sm: 12,
                  validation: Yup.string()
              },*/

            google_drive: {
                  title: 'Google Drive',
                  sm: 12,
                  validation: Yup.string()
              },

        };

        const backLink = (this.props.id === 'new') ? "/deals/" + this.props.deals_id : "/services/" + this.props.id;
        return (
            <div>
            <PageHeader
                header = {this.props.data.name}
                subHeader =  { this.props.data.comment }
            />
            <Container fluid style={{ paddingLeft: 40, paddingRight: 40}}>

            <FormikForm fieldList = {fieldsList}
                        onSubmit = {this.onSubmit}
                        initialValues = {this.props.data}
                        onSuccessLink = {backLink}
                        {...this.props}
              />
        </Container>
      </div>

    );

    }

};


export default (props) => {
    let new_api;
    if ((props.match.params.id === 'new') && (props.match.params.group_id !== undefined)) {
       new_api = `/api/groups/${props.match.params.group_id}/services/`;
    }

    const Wrapper = DetailDataLoaderWrapper(FormView, api,  resource, new_api);
    return <Wrapper {...props}/>;
}