import React, { Component } from "react";
import * as Yup from 'yup';
import {Card, Tab, Nav, Container, Row, Button, Col, ListGroup, Form, InputGroup} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import FormikForm from "../../components/Forms/FormikForm"

import {api, resource} from "./_config";


class FormView extends Component {

    render() {

        const fieldsList = {
            name: {
                title: '',
                validation: Yup.string().required('Required'),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },
            
            firstName: {
                title: '',
                validation: Yup.string().required('Required'),
                is_company: false,
                is_person: true,
                is_enterpreneur: true
            },
            surnameName: {
                title: '',
                validation: Yup.string().required('Required'),
                is_company: false,
                is_person: true,
                is_enterpreneur: true
            }
            ,
            birthday: {
                title: ' ',
                validation: Yup.date(),
                is_company: false,
                is_person: true,
                is_enterpreneur: false
            },
            
            passport_no: {
                title: ' ',
                validation: Yup.string(),
                sm: 6,
                is_company: false,
                is_person: true,
                is_enterpreneur: false
            },
            
            passport_date: {
                title: ' ',
                validation: Yup.string(),
                sm: 6,
                is_company: false,
                is_person: true,
                is_enterpreneur: false
            },
            
            passport_issuer: {
                title: ' ',
                validation: Yup.string(),

                is_company: false,
                is_person: true,
                is_enterpreneur: false
            },
            
            address: {
                title: '',
                validation: Yup.string(),
                is_company: true,
                is_person: true,
                is_enterpreneur: true},
            
            INN: {
                title: '',
                validation: Yup.string(),
                is_company: true,
                is_person: true,
                is_enterpreneur: true
            },

            KPP: {
                title: '',
                validation: Yup.string(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },

            snils: {
                title: '',
                validation: Yup.string(),
                is_company: false,
                is_person: true,
                is_enterpreneur: false
            },
            
            BankAccount: {
                title: ' ',
                validation: Yup.string(),
                is_company: true,
                is_person: true,
                is_enterpreneur: true
            },


            Bank: {
                title: '',
                validation: Yup.string(),

                is_company: true,
                is_person: true,
                is_enterpreneur: true
            },

            BIK: {
                title: '',
                validation: Yup.string(),
                is_company: true,
                is_person: true,
                is_enterpreneur: true
            },

            CorrespondAccount: {
                title: '.',
                validation: Yup.string(),
                is_company: true,
                is_person: true,
                is_enterpreneur: true
            },
            
            OGRN: {
                title: '',
                validation: Yup.string(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },
            OGRNIP: {
                title: '',
                validation: Yup.string(),
                is_company: false,
                is_person: false,
                is_enterpreneur: true
            },

            OKPO: {
                title: '',
                validation: Yup.string().required(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },
            
            dirJobTitle: {
                title: ' ',
                validation: Yup.string().required(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },

            dirSurname: {
                title: ' ',
                validation: Yup.string().required(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },

            dirFirstName: {
                title: ' ',
                validation: Yup.string().required(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },

            proxy: {
                title: '  ',
                validation: Yup.string().required(),
                is_company: true,
                is_person: false,
                is_enterpreneur: false
            },

        };

        const criteria_names = {
            1: 'is_company',
            2: 'is_person',
            3: 'is_enterpreneur'
        };

        const criteria = criteria_names[this.props.data.type];

        Object.entries(fieldsList).filter(entry => !entry[1][criteria]).forEach(item => delete fieldsList[item[0]])

        return (
            <div>
            <PageHeader
                header = { `  ${this.props.data.name}` }
                subHeader =  { this.props.data.comment }
            />
            <Container fluid style={{ paddingLeft: 40, paddingRight: 40}}>

            <FormikForm fieldList = {fieldsList}
                        onSubmit = {this.onSubmit}
                        initialValues = {this.props.data}
                        onSuccessLink = {"/suppliers/" + this.props.data.parent + "/payers"}
                        {...this.props}
              />
        </Container>
      </div>

    );

    }

};


export default (props) => {
    let new_api;
    if ((props.match.params.id.startsWith('new')) && (props.match.params.supplier_id !== undefined)) {
       new_api = `/api/suppliers/${props.match.params.supplier_id}/payers/`;
    }

    const Wrapper = DetailDataLoaderWrapper(FormView, api, resource, new_api);
    return <Wrapper {...props}/>;
}