import React, {Component} from "react";
import InfiniteScrollListView from "../../components/ListViews/InfiniteScrollListView";
import TableSubHeader from "../../components/ListViews/TableSubHeader";


class ListHelper extends Component {

    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data, orderBy) {
        let prevLetter="0", sort;
        /*
        if (orderBy.startsWith("-")) {
            orderBy = orderBy.substr(1); // Remove "-" at the beginning
            sort = (a,b) => (a[orderBy] < b[orderBy]) ? -1: 1;
        } else sort = (a,b) => (a[orderBy] > b[orderBy]) ? -1: 1;
        */
        const items = Object.values(data)
            .sort(this.sort)
            .map(item => {
                let header;
                const firstLetter = (item.name!=="") ? item.name.substr(0, 1) : "";
                // Generates header when we found new service type
                if (firstLetter.toLowerCase() !== prevLetter.toLowerCase()) {
                    prevLetter = firstLetter;
                    header = <TableSubHeader colSpan={6} header={firstLetter} />
                }
                return [ header, this.renderOneItem(item, header) ];
                });

        return items;
    }

    /**
     * Sets sort criteria
     * For this case: by Name
     */

    sort = (a,b) => (a.name.toLowerCase() < b.name.toLowerCase()) ?  -1: 1;

    /** Render a Line of Data  */

    renderOneItem = (item) => {

        const toolBar = (this.props.renderLineToolbar) ? <td>{this.props.renderLineToolbar(item.id, this.props.updateData)}</td> : undefined;
        return <>
            <tr key={item.id}>
                {/* Data */}
                <td onClick={() => {this.props.history.push(`/suppliers/${item.id}`);}}>{item.name}</td>
                <td onClick={() => {this.props.history.push(`/suppliers/${item.id}`);}}>{item.job}</td>
                <td onClick={() => {this.props.history.push(`/suppliers/${item.id}`);}} align="center">{item.phone}</td>
                <td onClick={() => {this.props.history.push(`/suppliers/${item.id}`);}} align="center">{item.email}</td>
                <td onClick={() => {this.props.history.push(`/suppliers/${item.id}`);}} align="center">{item.website}</td>
                {/* Toolbar */}
                {toolBar}
            </tr>
        </>
    };

    /** Renders component with Infinite Scroll*/

    render() {
        const items = this.renderItems(this.props.data);

        const tableHeader = [
            {name: "Name", width: "25%"},
            {name: "Job", width: "25%"},
            {name: "Phone", width: "25%", align: "center"},
            {name: "Email", width: "25%", align: "center"},
            {name: "Website", width: "25%", align: "center"}
        ];

        return <InfiniteScrollListView tableHeader={tableHeader} items={items} {...this.props} />
    }

};

export default ListHelper;