import React, { Component } from "react";
import * as Yup from 'yup';
import FormikModal from "../../components/Forms/FormikModal"
import {api, resource} from "./_config";


class FormModal extends Component {

    render() {

        const fieldsList = {
            name: {
                  title: '',
                  validation: Yup.string().required('Required')
              },
            phone: {
                  title: '',
                  validation: Yup.string().required('Required')
              },

            email: {
                  title: 'Email',
                  validation: Yup.string().email()
              },

            web: {
                  title: 'Web',
                  validation: Yup.string().url()
              },

        };
         
        return (

                <FormikModal fieldList = {fieldsList}
                             title = {this.props.data.service_display_name}
                             api = {api}
                             resourse = {resource}
                             id = {this.props.data.id}
                             data = {this.props.data} >
                    {this.props.children}

                </FormikModal>
               );

    }

};


export default FormModal;