import React, { Component } from "react";
import * as Yup from 'yup';
import {Card, Tab, Nav, Container, Row, Button, Col, ListGroup, Form, InputGroup} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import FormikForm from "../../components/Forms/FormikForm"


class FormView extends Component {

    render() {

         const fieldsList = {
          name: {
              title: ' ',
              sm: 6,
              validation: Yup.string().required('Required')
          },
          date: {
              title: '',
              sm: 6,
              validation: Yup.date('Incorrect data format').required('   ')
          },

          client: {
              title: '',
              type: 'autocomplete',
              resource: 'clients',
              api: '/api/clients/select/'
          },

          sum_plan: {
              title: '',
              sm: 12,
              validation: Yup.number().required()
          },

         official_name: {
              title: ' ',
              sm: 12,
              validation: Yup.string()
          },


          google_drive: {
              title: 'Google Drive',
              sm: 12,
              validation: Yup.string()
          },

        };

        const backLink = (this.props.id !== 'new') ? "/deals/" + this.props.id : "/deals";

        return (
            <div>
            <PageHeader
                header = { ` ${this.props.data.name}  ${this.props.data.date}` }
                subHeader =  ""
            />
            <Container fluid style={{ paddingLeft: 40, paddingRight: 40}}>


            <FormikForm fieldList = {fieldsList}
                        onSubmit = {this.onSubmit}
                        initialValues = {this.props.data}
                        onSuccessLink = {backLink}
                        {...this.props}
              />
        </Container>
      </div>

    );

    }

};


const Wrapper = DetailDataLoaderWrapper(FormView, '/api/deals/',  'deals');
export default Wrapper;
