import React from "react";
import { Link } from "react-router-dom"
import WindowWidget from "../../components/WindowWidget";
import {api, resource} from "./_config";
import ButtonLink from "../../components/ButtonLink";
import {ListGroup} from "react-bootstrap";


export const InfoWidget = (props) => {

    const rightToolbar = <ButtonLink to={"/" + resource + "/" + props.id + "/edit"} title={"Edit"} />;

    return  <WindowWidget title={"Info"} rightToolbar={rightToolbar}>
        <ListGroup variant="flush">
            <ListGroup.Item><p style={{whiteSpace: 'pre-line'}}>{ props.data.description }</p></ListGroup.Item>
              <ListGroup.Item> Deal: <Link to={"/deals/" + props.data.deal}>{ props.data.deal_display }</Link><br/>
                Deadline: { props.data.date }<br/>
                GitHub: { props.data.github }<br/>
                Landing page: { props.data.landing_url }<br/>
                Video: { props.data.video_url } <br/></ListGroup.Item>
        </ListGroup>

            </WindowWidget>

};

export default InfoWidget;