import React, {Component} from "react";
import {Container} from "react-bootstrap";

import {ListDataLoader} from "../../components/DataLoaders/DataLoaderList"
import PageHeader from "../../components/PageHeader";
import ListHelper from "./ListHelper";
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog";
import {api, resource} from "./_config";
import ButtonLink from "../../components/ButtonLink";


/**
 * Render Supplier List Page for /suppliers/
 */
class ListView extends Component {

    /**
     * Defines Line Toolbar for delete operations
     * @param id - id for Line
     * @returns JSX code which will be places in <td> </td> component
     */

    renderLineToolbar = (id, updateData) => {
        return <DeleteDialog api={api} resource={resource} id={id} updateData={updateData}/>
    }

    render() {

        return <>
            <PageHeader header="Suppliers"
                        subHeader={"SubHeader"}
                        resource={resource}
                        searchBar
            />
            <Container fluid style={{paddingLeft: 40, paddingRight: 40}}>
                <ButtonLink title={"+ Add"} to={"/suppliers/new/edit"}/>
                <ListDataLoader api={api} resource={resource}>
                    <ListHelper
                        renderLineToolbar={this.renderLineToolbar}
                        history={this.props.history}/>
                </ListDataLoader>
            </Container>
        </>
    }
}


export default ListView;