import React, { Component } from "react";
import { Container } from "react-bootstrap";
import * as Yup from 'yup';

import PageHeader from "../../components/PageHeader";
import FormikForm from "../../components/Forms/FormikForm";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import {api, resource} from "./_config";


class FormView extends Component {

    render() {

         const fieldsList = {
          name: {
              title: '',
              sm: 12,
              validation: Yup.string().required('Required')
          },
          int_name: {
              title: ' ',
              sm: 12,
              validation: Yup.string()
          },

          price: {
              title: '  ',
              sm: 6,
              validation: Yup.number().required()
          },


          cost: {
              title: ' ',
              sm: 6,
              validation: Yup.number().required()
          },

          /*
          measure: {
              title: ' ',
              type: 'autocomplete',
              resource: 'measure',
              api: '/api/select/measure/'
          },
          */

         service: {
              title: '',
              type: 'autocomplete',
              resource: 'services',
              api: '/api/services/select/'
          },

          tz: {
              title: '',
              type: 'textarea',
              validation: Yup.string()
          },

        };

        const backLink = (this.props.id === 'new') ? "/services/" + this.props.data.service + "/options": "/options/" + this.props.id;
        return <>
                <PageHeader
                    header = { `   ${this.props.data.service_display}` }
                    subHeader =  { this.props.data.comment }
                />
                <Container fluid style={{ paddingLeft: 40, paddingRight: 40}}>
                    <FormikForm fieldList = {fieldsList}
                                onSubmit = {this.onSubmit}
                                initialValues = {this.props.data}
                                onSuccessLink = {backLink}
                                {...this.props}
                      />
                </Container>
              </>

    }

};


export default (props) => {
    let new_api;
    if ((props.match.params.id === 'new') && (props.match.params.service_id !== undefined)) {
       new_api = `/api/services/${props.match.params.service_id}/options/`;
    }

    const Wrapper = DetailDataLoaderWrapper(FormView, api,  resource, new_api);
    return <Wrapper {...props}/>;
}