import React, { Component } from "react";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { numberWithCommas } from "../../utils/formaters";
import TableListView from "../../components/ListViews/TableListView";
import ButtonLink from "../../components/ButtonLink";


/**
 * PayerList manages whole finance table
 *
 * @author: Mikhail Lazarev
 */

class ListWidget extends Component {

    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data, orderBy) {
        let num=0, sort;

        if (orderBy.startsWith("-")) {
            orderBy = orderBy.substr(1); // Remove "-" at the beginning
            sort = (a,b) => (a[orderBy] < b[orderBy]) ? -1: 1;
        } else sort = (a,b) => (a[orderBy] > b[orderBy]) ? -1: 1;

        const items = Object.values(data)
        .sort(sort)
        .map(item => this.renderOneItem(num++, item)
            );

        return items;
    }

    /** Render Items (with header if needed) */

    renderOneItem(num, item) {

        const funcLinkOption = () => this.props.history.push(`/payers/${item.id}`);

        return  <tr onClick={funcLinkOption} >
                    <td>{item.type_display}</td>
                    <td>{item.name}</td>
                    <td style={{textAlign: 'center'}}>{item.INN}</td>
                    <td style={{textAlign: 'center'}}>{item.address }</td>
                    <td></td>
               </tr>
    }

    /** Render Toolbar */

    renderToolbar() {

        return  <>
                    <ButtonLink to={`/suppliers/${this.props.id}/payers/new_company`} title={'+  . '} />
                    <ButtonLink to={`/suppliers/${this.props.id}/payers/new_person`} title={'+  '} />
                    <ButtonLink to={`/suppliers/${this.props.id}/payers/new_enterpreneur`} title={'+  . '} />
            </>
    }

    /** Render component function */

    render() {

       const items = this.renderItems(this.props.data, "date");

       const tableHeader = [
            {name: "", width: "5%"},
            {name: "", width: "55%"},
            {name: "", width: "15%", align: "center"},
            {name: "", width: "30%", align: "center"},
        ];

        const toolbar = this.renderToolbar();

        return <TableListView toolbar={toolbar} tableHeader={tableHeader} items={items} />
    }

};


export default ListWidget;