import React, { Component } from "react";
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import {Button} from "react-bootstrap";

import {numberWithCommas} from "../../utils/formaters";
import TableListView from "../../components/ListViews/TableListView";
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog_OLD";
import FormModal from "./FormModal";
import * as actions from "../../actions/modals";


/**
 * ContOptionList manages whole finance table
 *
 * @author: Mikhail Lazarev
 */

class ContOptionList extends Component {

    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data) {
         let num=0;

         const items = Object.values(data)
            .sort(this.sort)
            .map(item => this.renderOneItem(num++, item)
                );

         return items;
    }

    /** Sort items lambda function */

    sort = (a,b) => (a.last_update > b.last_update) ? -1: 1;

    /** Render Items (with header if needed) */

    renderOneItem(num, item) {

        const funcLinkOption = () => this.props.showForm("contOptionsModal", item);

        return  <tr >

                <td><Link to={"/suppliers/" + item.supplier}>{ item.supplier_display }</Link></td>
                <td onClick={ funcLinkOption } align="center">{ numberWithCommas(item.cost) }</td>
                <td onClick={ funcLinkOption } align="center">{ item.last_update }</td>
                <td onClick={ funcLinkOption } align="center">{ item.user_updated_display }</td>
                <td onClick={ funcLinkOption } align="center">{ item.phone }</td>
                <td onClick={ funcLinkOption }>{ item.comment }</td>

                <td>
                    <DeleteDialog
                        api={'/api/cont_options/'}
                        id={item.id}
                        updateData = { this.props.updateData }
                        />
                </td>
           </tr>
    }


    /** Render Toolbar */
    renderToolbar = () => (
        <Button size={"sm"}
                onClick={() => this.props.createForm("contOptionsModal",
                    "/api/cont_options_set/" + this.props.data.id + "/cont_options/new/")}>
            + </Button>
    );

    /** Render component function */

    render() {

        const items = this.renderItems(this.props.data.cont_options);

        const tableHeader = [
            {name: "", width: "35%"},
            {name: ". ", width: "15%", align: "center"},
            {name: " ", width: "15%", align: "center"},
            {name: " ", width: "20%", align: "center"},
            {name: "", width: "15%", align: "center"},
        ];

        const modalForms =  <FormModal formId={"contOptionsModal"} updateData={this.props.updateData}/>
        const toolbar = this.renderToolbar();

       return <TableListView modalForms={modalForms} toolbar={toolbar} tableHeader={tableHeader} items={items} />
    }

};

const mapDispatchToProps = dispatch => ({
     createForm:      (formId, api) => dispatch(actions.createForm(formId, api)),
     showForm:      (formId, data) => dispatch(actions.showForm(formId, data))

});


export default connect(null, mapDispatchToProps)(ContOptionList);
