import React from "react";
import {Tab} from "react-bootstrap";
import {Link} from "react-router-dom";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import InfoWidget from "./InfoWidget";
import ContOptionsList from "../ContOptions/ListWidget"
import BudgetList from "../Budgets/ListWidgetEvent";
import {api, resource} from "./_config";
import TabBar from "../../components/TabBar/TabBar";


function DetailView(props) {

    let content_tab;

    if ((props.data.type === 's') && (props.data.cont_optionSet)) {
        content_tab = <Tab.Pane eventKey="cont_options" label={""}>
            <ContOptionsList {...props} data={props.data.cont_optionSet}/>
        </Tab.Pane>
    } else {
        content_tab = <Tab.Pane eventKey="budget" label={""}>
            <BudgetList {...props} int={true} data={props.data.budgetSet}/>
        </Tab.Pane>
    }

    const service_display = <Link to={"/services/" + props.data.service}>{props.data.service_display}</Link>

    return (
        <>
            <PageHeader
                header={props.data.name}
                subHeader={service_display}
            />
            <TabBar id={props.id} resource={resource} tab={props.match.params.tab || 'info'} history={props.history}>

                <Tab.Pane eventKey="info" label={""}>
                    <InfoWidget data={props.data} id={props.id} {...props}/>
                </Tab.Pane>
                {content_tab}

                <Tab.Pane eventKey="operations" label={""}>

                </Tab.Pane>
            </TabBar>
        </>
    );

};

export default DetailDataLoaderWrapper(DetailView, api, resource);