import React from "react";
import {Button, Tab} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail"
import ListDataLoader from "../../components/DataLoaders/DataLoaderList";
import EventListWidgetDeal from "../Projects/ListWidgetDeal"
import InfoWidget from "./InfoWidget";
import {getFullAPIAddress} from "../../utils/api";
import {api, resource} from "./_config"
import TabBar from "../../components/TabBar/TabBar";


function DetailView(props) {

    const EventsWidget = ListDataLoader(EventListWidgetDeal, api + props.id + '/events/', 'events');

    return <>
        <PageHeader
            header={props.data.name}
            subHeader={props.data.date}
            icon={props.data.icon}
        />

        <TabBar id={props.id} resource={resource} tab={props.match.params.tab} history={props.history}>

            <Tab.Pane eventKey="info" label={"Info"}>
                <InfoWidget {...props} />
            </Tab.Pane>

            <Tab.Pane eventKey="projects" label={"Projects"}>
                <EventsWidget id={props.id} {...props}/>
            </Tab.Pane>

            <Tab.Pane eventKey="finances" label={"Finances"}>
                <Button size={"sm"} variant={'light'}></Button>
            </Tab.Pane>

        </TabBar>
    </>
};

export default DetailDataLoaderWrapper(DetailView, api, resource);