import React, { Component } from "react";
import {connect} from "react-redux";
import * as Yup from 'yup';
import {Badge, Popover, Button} from "react-bootstrap";
import { updateDataLoaderDetail } from "../../actions/dataLoader";

import FormikModal from "../../components/Forms/FormikModal"
import {getApiById} from "../../utils/api";
import {getHashUpdates} from "../../reducers";
import * as actions from "../../actions/dataLoader";
import * as status from "../../status";


export class GroupAddForm extends Component {

    render() {

        const fieldsList = {
            group: {
                  title: ' ',
                  sm: 12,
                  type: 'autocomplete',
                  resource: 'service_groups',
                  api: '/api/groups/select/'
              },
        };
         
        return (

                <FormikModal fieldList = {fieldsList}
                             title = {"  "}
                             api = {"/api/services/:id/add_to_group/"}
                             resourse = {'services_groups'}
                             id = {this.props.data.id}
                             data = {this.props.data} >
                    <Badge variant={"success"}></Badge>
                </FormikModal>
               );

    }

};

class DeleteGroupAction extends Component {


    state = {
        hashSent: 0
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const hash = this.state.hashSent;
        if (hash === 0) return;
        if ((this.props.hashUpdates[hash] !== undefined)) {
            this.setState({hashSent: 0});
            this.props.updateData();
        }

    }

    onDelete = (service_id, group_id) => {
        console.log(service_id, group_id)
        const hashSent = "DELETE " + service_id + "/" + group_id + Date()
        const api = "/api/services/:id/remove_from_group/"
        const resource = 'services_groups'
        const data = {id: service_id, group: group_id}
        this.setState({ hashSent: hashSent})
        this.props.updateDetail(api, resource, service_id, data, hashSent);
    }

    render() {
        return  (
            <Button variant={"danger"}
                    size={"sm"}
                    onClick={() => this.onDelete(this.props.data.id, this.props.id)}
            >
                
            </Button>);
    }


};

const mapStateToProps = (state) => ({
    hashUpdates:          getHashUpdates(state),

});

const mapDispatchToProps = dispatch => ({
     updateDetail:      (api, resource, id, data, hashSent) => dispatch(updateDataLoaderDetail(api, resource, id, data, hashSent)),

});

const DeleteGroupActionComponent = connect(mapStateToProps, mapDispatchToProps)(DeleteGroupAction);

export const GroupDelete = (props) => (

  <Popover id="popover-basic" title={"  ?"}>
      <DeleteGroupActionComponent {...props}/>
  </Popover>

);


export default GroupAddForm;