import React from "react";
import {Button, Container, Nav, Tab} from "react-bootstrap";

import PageHeader from "../../components/PageHeader";
import DetailDataLoaderWrapper from "../../components/DataLoaders/DataLoaderDetail";
import TabBar from "../../components/TabBar/TabBar";
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog_OLD";

import InfoWidget from "./InfoWidget";
import OptionListService from "../Options/ListWidget";
import GroupPopupWidget from "../Groups/PopupWidget";
import {api, resource} from "./_config";


function DetailView(props) {

    const onSuccessUrlAfterDelete = (props.data.groups) ? '/services/group/' + props.data.groups[0].id : "/services";

    return <>
            <PageHeader
                header={props.data.name}
                subHeader={<GroupPopupWidget {...props} data={props.data}/>}
            />
            <TabBar id={props.id} resource={resource} tab={props.match.params.tab || 'info'} history={props.history}>

                <Tab.Pane eventKey="info" label={""}>
                    <InfoWidget {...props}/>
                </Tab.Pane>

                <Tab.Pane eventKey="options" label={""}>
                    <OptionListService {...props} data={props.data.options}/>
                </Tab.Pane>

                <Tab.Pane eventKey="admin" label={""}>
                    <DeleteDialog api={api} id={props.id} onSuccessURL={onSuccessUrlAfterDelete} history={props.history}>
                        <Button variant={"danger"}></Button>
                    </DeleteDialog>

                </Tab.Pane>
            </TabBar>
        </>
};

export default DetailDataLoaderWrapper(DetailView, api, resource);