import React, { Component } from "react";
import { Button, Container, Modal, Form } from "react-bootstrap";
import { connect } from "react-redux"
import { MdEdit } from "react-icons/md";
import * as actions from "../../actions/dataLoader";
import * as status from "../../status";
import {getHashUpdates} from "../../reducers";
import GroupDetail from "../Groups/GroupDetail";
import "./Modal.css"

class ServiceSelectModal extends Component {

    state = {
      show: false,
        };

    handleClose =() => {
        this.setState({ show: false });
    }

    handleShow = () => {
        this.setState({ show: true });
    }

    onDataUpdate = () => {
        this.handleClose()
        this.updateData()
    }

  render() {

    const { children } = this.props;

    const childrenWithProps = React.Children.map(children, child =>
          React.cloneElement(child, { onClick: this.handleShow })
        );

    return (
      <>
          {childrenWithProps }

          <Modal
            show={this.state.show}
            onHide={this.handleClose}
            dialogClassName={"modal-90w"}

        >
          <Modal.Header closeButton >
            <Modal.Title></Modal.Title>
              <Form.Control>

              </Form.Control>
          </Modal.Header>
          <Modal.Body>
              <GroupDetail id={"service_root"} {...this.props} />
          </Modal.Body>

        </Modal>
      </>
    );
  }
}




export default ServiceSelectModal;