import React, { Component } from "react";
import {Card, Tab, Nav, Container, Row, Button, Col, CardImg, Table} from "react-bootstrap";
import { Link }from "react-router-dom";

import {numberWithCommas} from "../../utils/formaters";
import DeleteDialog from "../../components/DataUpdaters/DeleteDialog_OLD";
import TableListView from "../../components/ListViews/TableListView";
import ButtonLink from "../../components/ButtonLink";



/**
 * OptionList manages whole finance table
 *
 * @author: Mikhail Lazarev
 */

class ListWidget extends Component {

    /**
     * Converts data into JSX table
     * @param data
     * @returns JSX table
     */

    renderItems(data) {
         let num=0;

         const items = Object.values(data)
            .sort(this.sort)
            .map(item => this.renderOneItem(num++, item)
                );

         return items;
    }

    /**
     * Sort items lambda function
     *
     * For finances we use service_type to group items by service type
     *
     */

    sort = (a,b) => {
        if (a.name.toLowerCase() < b.name.toLowerCase()) return -1;
        return -1;
    }


    /** Render Items (with header if needed) */

    renderOneItem(num, item) {

        const funcLinkOption = () => this.props.history.push(`/options/${item.id}`);

        return  <tr  >
                <td onClick={ funcLinkOption }>{item.int_name}</td>
                <td onClick={ funcLinkOption }>{item.name}</td>
                <td onClick={ funcLinkOption } style={{textAlign: 'center'}}>{item.measure_display_name}</td>
                <td onClick={ funcLinkOption } align="center">{ numberWithCommas(item.price) }</td>
                <td onClick={ funcLinkOption } align="center">{ numberWithCommas(item.cost) }</td>
                <td align="center">
                    <DeleteDialog
                        api={'/api/options/'}
                        id={item.id}
                        updateData = { this.props.updateData }/>
                </td>
           </tr>
    }


    /** Render Toolbar */
    renderToolbar() {
        return  <ButtonLink to={`/services/${this.props.id}/options/new/`} title={"+ "} />
    }

    /** Render component function */

    render() {

        const items = this.renderItems(this.props.data);

        const tableHeader = [
            {name: ". ", width: "23%"},
            {name: "", width: "40%"},
            {name: ". ", width: "14%", align: "center"},
            {name: ". ", width: "14%", align: "center"},
            {name: "", width: "14%", align: "center"},
        ];

        const toolbar = this.renderToolbar();

        return <TableListView toolbar={toolbar} tableHeader={tableHeader} items={items} />
    }

};


export default ListWidget;
